<template>
  <div>
    <LayOut>
      <template #content>
        <a-form-model layout="inline" :model="formInline">
          <a-form-model-item>
            <a-input v-model="formInline.real_name" placeholder="受助人姓名"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.idCard" placeholder="受助人身份证"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.assistantName" placeholder="服务人员姓名"></a-input>
          </a-form-model-item>
        </a-form-model>
        <a-form-model layout="inline" :model="formInline">
          <a-form-model-item>
            <a-input v-model="formInline.disabledContact" placeholder="用户来电"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.userType" placeholder="用户类型"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.district" placeholder="受助人区域"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-button @click="onSubmit" type="primary" html-type="submit">查询</a-button>
          </a-form-model-item>
        </a-form-model>
        <a-divider></a-divider>
        <a-table :columns="columns" :pagination="pagination" :data-source="data">
          <span slot="action" slot-scope="record">
            <router-link :to="{path:'/disableddetail',query:{real_name:record.real_name}}">详情</router-link>
            <a-divider type="vertical" />
            <a-popconfirm
              title="您确定要删除吗?"
              ok-text="Yes"
              cancel-text="No"
              @confirm="confirm(record.real_name)"
              @cancel="cancel"
            >
              <a>删除</a>
            </a-popconfirm>

            <a-divider type="vertical" />
            <router-link :to="{path:'/qrcode',query:{real_name:record.real_name}}">二维码</router-link>
          </span>
        </a-table>
      </template>
    </LayOut>
  </div>
</template>
<script>
import { initDisableds } from "@/api/api";
import { queryDisableds } from "@/api/api";
import { delDisabled } from "@/api/api";
import LayOut from "@/views/LayOut.vue";
const columns = [
  {
    dataIndex: "real_name",
    key: "real_name",
    title: "受助人姓名"
  },
  {
    title: "区域",
    dataIndex: "district",
    key: "district"
  },
  {
    title: "性别",
    dataIndex: "sex",
    key: "sex"
  },
  {
    title: "用户类型",
    key: "userType",
    dataIndex: "userType"
  },
  {
    title: "服务人员姓名",
    key: "assistantName",
    dataIndex: "assistantName"
  },
  {
    title: "服务人员电话",
    key: "assistantContact",
    dataIndex: "assistantContact"
  },
  {
    title: "操作",
    key: "action",
    scopedSlots: { customRender: "action" }
  }
];

const data = [];
export default {
  data() {
    return {
      parentMsg: "hello",
      pagination: {
        total: 0,
        pageSize: 10, //每页中显示10条数据
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50", "100"], //每页中显示的数据
        showTotal: total => `共有 ${total} 条数据` //分页中显示总的数据
      },
      data,
      columns,
      formInline: {
        real_name: "",
        idCard: "",
        assistantName: "",
        disabledContact: "",
        userType: "",
        district: ""
      }
    };
  },
  async mounted() {
    let res = await initDisableds();
    console.log(res);
    this.data = res;
  },
  methods: {
    async onSubmit(e) {
      let res = await queryDisableds(this.formInline);
      this.data = res;
    },
    async confirm(e) {
      console.log(e);
      // this.$message.success("点击 Yes");
      let res = await delDisabled(e);
      if (res.data.errmsg == "ok") {
        this.$message.success("删除成功");
        let res = await await initDisableds();
        this.data = res;
      }
    },
    cancel(e) {
      console.log(e);
      // this.$message.error("点击 No");
    }
  },
  components: {
    LayOut
  }
};
</script>
